h2 {
    margin-bottom: 1em;
}
.sectionOneContainer {
    display: flex;
    justify-content: center;
    line-height: 170%;
}

.sectionOneText {
    padding: 5%;
    background-color: #ABB7B7;
    max-width: 60%;
}

.sectionOneTextMobile {
    padding: 5%;
    background-color: #ABB7B7;
    max-width: 100%;
}

.sectionTwoText {
    padding: 5%;
    background-color: white;
    line-height: 170%;
    max-width: 50%;
}

.sectionTwoTextMobile {
    padding: 5%;
    background-color: white;
    line-height: 170%;
    max-width: 100%;
    text-align: center;
}

.sectionTwoFiller {
    display: flex;
    justify-content: center;
    background-color: white;
    line-height: 170%;
    width: 50%;
}

.sectionThreeContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5%;
    background-color: #ABB7B7;
    line-height: 170%;
    max-width: 100%;
}

.sectionThreeText {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 60%;
    margin-top: 1em;
}

.sectionThreeTextMobile {
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 100%;
    margin-top: 1em;
}

.image {
    object-fit: cover;
    max-width: 40%;
}

.imageMobile {
    object-fit: cover;
    max-width: 100%;
}

.flex {
    display: flex;
}

.desktopTitle {
    font-family: 'Lato';
    font-weight: lighter;
    margin-left: 5%;
}

#title {
    font-size: 35px;
}
.desktopNav {
    height: 20vh;
    background-color: #ECF0F1;
    color: #2980B9;
    max-width: 100%;
}

.mobileNavContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 100%;
}

.mobileTitle {
    font-size: 35px;
    font-family: 'Lato';
    font-weight: lighter;
    text-align: center;
}

.mobileNav {
    height: 20vh;
    background-color: #ECF0F1;
    color: #2980B9;
    display: flex;
    justify-content: center;
    align-items: center;
}
